<template>
    <v-container fluid class="container" @click="timer_logout_set()">
        <layout-header />
        <v-row class="contents" align="center" justify="center">
            <list-menu-list
                class="content-body"
                title="스트레칭"
                :switchItems="switchItems"
                :thumbnailList="thumbnailList"
            />
        </v-row>
    </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import exercise from '@/mixins/exercise.js'
import timer_logout from '@/mixins/timer_logout.js'

export default {
    name: 'Stretching',
    mixins:[ exercise, timer_logout ],
    data:()=>({
        thumbnailList: []
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_EX_RESULT_LIST',
            'GET_MENU_STRETCH_EX_PROGRAM_LIST',
            'GET_MENU_STRETCH_EX_SORT_LIST'
        ]),
        switchItems(){
            return this.exercise_stretching_switch_items
        }
    },
    async created() {
        await this.GetExResultList()
        await this.ACT_MENU_API_GET_STRETCH_EX_PROGRAM_LIST()
        .then(()=>this.EditProgramList())

        await this.ACT_MENU_API_GET_STRETCH_EX_SORT_LIST()
        .then(()=>this.EditSortList())
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_EX_RESULT_LIST',
            'ACT_MENU_API_GET_STRETCH_EX_PROGRAM_LIST',
            'ACT_MENU_API_GET_STRETCH_EX_SORT_LIST',
            'ACT_MENU_API_GET_PROGRAM_LIST',
        ]),
        ...mapMutations([
            'MU_MENU_STRETCH_LIST',
            'MU_MENU_STRETCH_EX_PROGRAM_LIST',
            'MU_PROGRAM_PLAY_TOTAL_SET',
        ]),
        async GetExResultList(){
            // 운동 이력 가져오기
            const exType = 0 // 서킷을 제외한 나머지는 0 (프리웨이트, 오늘의 운동, 스트레칭)
            await this.ACT_MENU_API_GET_EX_RESULT_LIST(exType)
        },
        async EditProgramList(){
            let seochoExerciseData = []
            for( const item of this.GET_MENU_STRETCH_EX_PROGRAM_LIST){
                item.name       = item.programTitle
                item.link       = 'program'
                item.switchType = 0

                // 이미지 추가
                item.image  = this.exercise_create_image(item.exSortCode)

                // 운동 이력 체크
                let check = this.GET_MENU_EX_RESULT_LIST.exList.find(list => list.exSortCode === item.exSortCode)
                if(check !== undefined) item.check = true

                // program 추가
                let Program  = await this.ACT_MENU_API_GET_PROGRAM_LIST(item.programNo)
                for( const programItem of Program.setList){
                    programItem.image       = this.exercise_create_image(programItem.subExSortCode)
                    programItem.video_ex    = this.exercise_create_ex_video(programItem.subExSortCode)
                    programItem.video_rest  = this.exercise_create_rest_video(programItem.subExSortCode)
                    programItem.name        = programItem.subExname
                }
                item.program = Program.setList
                
                if(item.exSortCode === 6203 || item.exSortCode === 6204){
                    // 서초구 운동 예외처리
                    seochoExerciseData.push(item)
                }else{
                    this.thumbnailList.push(item)
                }
            }

            // 서초구 운동 앞으로 추가
            if(seochoExerciseData.length !== 0){
                for( const item of seochoExerciseData){
                    this.thumbnailList.unshift(item)
                }
            }

            this.MU_MENU_STRETCH_EX_PROGRAM_LIST(this.thumbnailList)
            // console.log('Program List',this.thumbnailList)

            // 스트레칭 Total Set 등록
            const playSet = 1
            this.MU_PROGRAM_PLAY_TOTAL_SET(playSet)
        },
        EditSortList(){
            this.GET_MENU_STRETCH_EX_SORT_LIST.forEach(item => {
                item.name       = item.exSortname
                item.link       = 'PartPlay'
                item.tabType    = item.bodyPartCode
                item.exMode     = 1
                item.switchType = 1
                
                // 이미지 추가
                item.image  = this.exercise_create_image(item.exSortCode)

                // video 추가
                item.video = this.exercise_create_ex_video(item.exSortCode)

                // 운동 이력 체크
                let check = this.GET_MENU_EX_RESULT_LIST.exList.find(list => list.exSortCode === item.exSortCode)
                if(check !== undefined) item.check = true
                
                this.thumbnailList.push(item)
            })
            this.MU_MENU_STRETCH_LIST(this.thumbnailList)
            // console.log('check', this.GET_MENU_EX_RESULT_LIST)
        },
    }
}
</script>

<style scoped lang="scss">
</style>